import React, { useState } from 'react'
import styled from 'styled-components'
import { NavLink, useLocation, useHistory, Redirect } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { saveAllCandidates, saveCurrentCandidate } from '../Actions/CandidateActions'
import { saveAllJobs, saveCurrentJob } from '../Actions/JobsActions'

const activeStyles = {
    backgroundColor: '#f1f1f1'
}

function SideBar() {
    const dispatch = useDispatch()
    const [page, setPage] = useState(1)



    return (
        <SidebarContainer>
            <Routes activeStyle={page === 1 ? activeStyles : {}} onClick={() => setPage(1)} to='/'>Dashboard</Routes>
            <Routes activeStyle={page === 2 ? activeStyles : {}} onClick={() => setPage(2)} to='/jobs'>Jobs</Routes>
            <Routes activeStyle={page === 3 ? activeStyles : {}} onClick={() => setPage(3)} to='/candidates'>Candidates</Routes>

        </SidebarContainer>
    )
}

export default SideBar

const SidebarContainer = styled.div`
    background-color: white;
    width: 12%;
    display: flex;
    flex-direction: column;
`

const Routes = styled(NavLink)`
    text-decoration: none;
    color: grey;
    font-weight: bold;
    text-align: left;
    padding: 10px;
    &:hover{
        
        text-decoration: none;
        background-color: white;
    }

    &:active{
        text-decoration: none;
    }

    

    

`