import React from 'react'
import styled from 'styled-components'
import { NavLink } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { logout } from '../Actions/AuthActions'

const activeStyles = {
    backgroundColor: '#00000029'
}




function NavBar() {
    const dispatch = useDispatch()

    const logoutHandler = () => {
        dispatch(logout(false))
    }

    return (
        <Header>
            <StyledNavLink to="/">Admin</StyledNavLink>
            <NavControls>
                <Logout onClick={logoutHandler}>Logout</Logout>
                {/* <StyledNavLink activeStyle={activeStyles} to="/profile">Logout</StyledNavLink> */}
            </NavControls>
        </Header>

    )
}

export default NavBar

const Logout = styled.button`
        color: white;
    background: #832525;
    border-style: none;
    padding: 0px 21px;
    font-weight: bold;

`







// Styled Components
const Header = styled.header`
position: sticky;
top: 0;
    color: white;
    background-color: #276EB0;
    display: flex;
    justify-content: space-between;
    /* padding: 10px 20px; */
    /* box-shadow: 0px 5px 20px 1px #00000017; */
`

const NavControls = styled.div`

    display: flex;
    justify-content: space-between;
    width: fit-content;
`

const Logo = styled.p`
    margin: 0px;
    padding: 0px;
`

const StyledNavLink = styled(NavLink)`
padding: 10px 20px;
    color: white;
    text-decoration: none;
    font-weight: bold;
    &:hover{
        background-color: #00000029;
        text-decoration: none;
        color: white;
    }
    &:active{
        text-decoration: none;
        color: white;
    }
`