import React, { useState } from 'react'
import { Card, Title, CardTitle, Container } from '../Common/Styles/StyledComponents'
import styled, { css } from 'styled-components'
import CloseSVG from '../Assets/Icons/close.svg'
import axios from 'axios'
import { API_URI } from '../Endpoint'

/*
title text
description textarea
salary text

experience text
responsibilities array
eligibility array

company text
aboutCompany text
location text

*/

function PostJob() {

    const [title, setTitle] = useState()
    const [salary, setSalary] = useState()
    const [description, setDescription] = useState()

    const [company, setCompany] = useState()
    const [aboutCompany, setAboutCompany] = useState()
    const [location, setLocation] = useState()

    const [experience, setExperience] = useState()

    const [eligibilitiesVal, setEligibilitiesVal] = useState()
    const [eligibilities, setEligibilities] = useState([])

    const [responsibilitiesVal, setResponsibilitiesVal] = useState()
    const [responsibilities, setResponsibilities] = useState([])

    const isInputValid = title && description && company && aboutCompany && location && experience && eligibilities.length > 0 && responsibilities.length > 0


    const resetHandler = () => {
        setTitle('')
        setSalary('')
        setDescription('')
        setCompany('')
        setLocation('')
        setAboutCompany('')
        setExperience('')
        setEligibilities([])
        setEligibilitiesVal('')
        setResponsibilities([])
        setEligibilitiesVal('')
        document.getElementById('title').focus()
    }

    const postHandler = async () => {
        const data = {
            title,
            salary: salary ? salary : 'Not disclosed',
            description,
            company,
            aboutCompany,
            location,
            experience,
            eligibility: eligibilities,
            responsibilities
        }
        console.log(data)
        try {
            const response = await axios.post(`${API_URI}create`, data)
            console.log(response)
        } catch (err) {
            console.log(err)
        }

    }

    const deleteItemHandler = (data, item, setCurrState) => {
        const FilteredData = data.filter(el => el !== item)
        setCurrState(FilteredData)
    }

    const renderList = (data, setCurrState) => {
        const list = data.map(item => {
            return <Element key={item}>
                <ElementP>{item}</ElementP>
                <Icon onClick={() => deleteItemHandler(data, item, setCurrState)} src={CloseSVG} />
            </Element>
        })

        return list.length > 0 ? list : 'Empty'
    }

    const addHandler = (currState, setCurrState, item) => {
        const TEMP = [...currState]
        TEMP.push(item)
        setCurrState(TEMP)
        setEligibilitiesVal('')
        setResponsibilitiesVal('')
    }


    return (

        <Container>
            <Form>

                <MainTitle>Post Job</MainTitle>
                <Card>
                    <CardTitle>Post Details</CardTitle>
                    <div>
                        <Btn color={'#d21e11'} onClick={resetHandler}>Reset</Btn>
                        <Btn color={'#088400'} disabled={!isInputValid} onClick={postHandler}>Post</Btn>
                    </div>
                </Card>
                <InfoCardContainer>
                    <Card>
                        <CardTitle>Job Details</CardTitle>
                        <InputContainer>
                            <Label>Job Title</Label>
                            <Input id="title" value={title} onChange={(e) => setTitle(e.target.value)} type="text" />
                        </InputContainer>
                        <InputContainer>
                            <Label>Salary</Label>
                            <Input value={salary} onChange={(e) => setSalary(e.target.value)} type="text" />
                        </InputContainer>
                        <InputContainer>
                            <Label>Description</Label>
                            <Textarea value={description} onChange={(e) => setDescription(e.target.value)} type="text" />
                        </InputContainer>
                    </Card>
                    <Card>
                        <CardTitle>Company Details</CardTitle>
                        <InputContainer>
                            <Label>Name</Label>
                            <Input value={company} onChange={(e) => setCompany(e.target.value)} type="text" />
                        </InputContainer>
                        <InputContainer>
                            <Label>Location</Label>
                            <Input value={location} onChange={(e) => setLocation(e.target.value)} type="text" />
                        </InputContainer>
                        <InputContainer>
                            <Label>About</Label>
                            <Textarea value={aboutCompany} onChange={(e) => setAboutCompany(e.target.value)} type="text" />
                        </InputContainer>
                    </Card>
                    <Card>
                        <CardTitle>Requirements</CardTitle>
                        <InputContainer>
                            <Label>Experience</Label>
                            <Input value={experience} onChange={(e) => setExperience(e.target.value)} type="text" />
                        </InputContainer>
                        <InputContainer>
                            <Label>Eligibility</Label>
                            <InputWithButton onSubmit={(e) => {
                                e.preventDefault()
                                addHandler(eligibilities, setEligibilities, eligibilitiesVal)
                            }}
                            >
                                <CustomInput value={eligibilitiesVal} onChange={(e) => setEligibilitiesVal(e.target.value)} type="text" />
                                <AddButton >Add</AddButton>
                            </InputWithButton>
                        </InputContainer>
                        <InputContainer>
                            <Label>Responsibilities</Label>
                            <InputWithButton onSubmit={(e) => {
                                e.preventDefault()
                                addHandler(responsibilities, setResponsibilities, responsibilitiesVal)
                            }}>
                                <CustomInput value={responsibilitiesVal} onChange={(e) => setResponsibilitiesVal(e.target.value)} type="text" />
                                <AddButton>Add</AddButton>
                            </InputWithButton>

                        </InputContainer>

                    </Card>
                    <RequirementCard>
                        <CardTitle>Eligibility</CardTitle>
                        <Ul>
                            {renderList(eligibilities, setEligibilities)}
                        </Ul>
                        <CustomLabel>Responsibilities</CustomLabel>
                        <Ul>

                            {renderList(responsibilities, setResponsibilities)}

                        </Ul>

                    </RequirementCard>
                </InfoCardContainer>

            </Form>

        </Container>
    )
}

export default PostJob

const InputContainer = styled.div`
    display: flex;
    flex-direction: column;
    margin: 0px 0px 10px 0px;
`
const Label = styled.label`
    /* font-weight: bold; */
    color: grey;
`

const InputStyles = css`
    padding: 5px 12px;
    margin: 10px 0px 0px 0px;
    border: 1px solid #00000017;
    border-radius: 5px;
    &:focus{
        outline: none;
    }
`

const Textarea = styled.textarea`
    ${InputStyles}
`
const Input = styled.input`
    ${InputStyles}
`

const InfoCardContainer = styled.div`
    display: flex;
`

const Form = styled.div`
    display: flex;
    flex-direction: column;
`

const InputWithButton = styled.form`
    margin-top: 10px;
    display:flex;
    justify-content: space-between;
    align-items: center;
    
`

const AddButton = styled.button`
    border-style: none;
    color: white;
    background-color: green;
    padding: 5px 8px;
    &:focus{
        outline: none;
    }
    border-radius: 0px 5px 5px 0px;
`

const CustomInput = styled.input`
    ${InputStyles}
    margin: 0px;
`

const RequirementCard = styled(Card)`
    height: 270px;
    width: 200px;
    overflow-y: scroll;
`

const Ul = styled.ul`
    margin: 0px;
    padding: 0px;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
`

const Element = styled.li`
    
    display: flex;
    align-items: center;
    margin: 0px 10px 10px 0px;
    padding:2px 10px;
    border-radius: 10px;
    border: 1px solid #276eb0;
    width: fit-content;
`

const ElementP = styled.p`
    margin: 0px;
    padding: 0px;
`

const Icon = styled.img`
    margin-left: 10px;
`

const CustomLabel = styled(CardTitle)`
    background-color: white;
    position: sticky;
    top: 0;
`

const Btn = styled.button`
    border-style: none;
    padding: 7px 10px;
    width: 130px;
    margin-right: 10px;
    color: white;
    background-color: ${props => props && props.color};

`

const MainTitle = styled(Title)`
    margin-top: 20px;
    margin-left: 20px;
`