import React, { useEffect, useState } from 'react'
import { Title, FlexDiv, Container, CustomCard, Card } from '../Common/Styles/StyledComponents'
import axios from 'axios'
import { API_URI } from '../Endpoint'
import { useGetData } from '../Hooks/getData'
import styled, { css } from 'styled-components'
import JobSVG from '../Assets/Icons/job.svg'
import PeopleSVG from '../Assets/Icons/people.svg'
import Loader from '../Assets/Icons/loader.svg'


function DashboardPage() {
    const [info, setInfo] = useState()

    useEffect(() => {
        axios.get(`${API_URI}info/counts`)
            .then(({ data }) => setInfo(data))
            .catch(err => console.log(err))

    }, [])


    console.log(info)



    return (
        <Container>
            <FlexDiv>
                <Title>Dashboard</Title>
            </FlexDiv>
            <CardContainer>

                <InfoCard>
                    <div>
                        <InfoTitle>Jobs</InfoTitle>
                        <InfoData>{info && info.jobsCount ? info.jobsCount : <img src={Loader} />}</InfoData>
                    </div>
                    <Icon src={JobSVG} />
                </InfoCard>
                <InfoCard>
                    <div>
                        <InfoTitle>Applicants</InfoTitle>
                        <InfoData>{info && info.usersCount ? info.usersCount : <img src={Loader} />}</InfoData>
                    </div>
                    <Icon src={PeopleSVG} />
                </InfoCard>
            </CardContainer>


        </Container>
    )
}

export default DashboardPage


const NoSpacing = css`

    margin: 0px;
    padding: 0px;

`
const InfoTitle = styled.p`
    ${NoSpacing}
    margin-bottom: 5px;
    opacity: 0.5;
    font-weight: bold;
    font-size: 0.9em;
`

const InfoData = styled.p`
    ${NoSpacing}
    font-weight: bold;
    font-size: 1.7em;
`

const InfoCard = styled(Card)`
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 150px;
`

const Icon = styled.img`
    background-color: #276eb0;
    border-radius: 50px;
    padding: 6px;

`

const CardContainer = styled.div`
    display: flex;
    flex-wrap: wrap;
`